@import url('https://fonts.googleapis.com/css2?family=Exo+2:wght@200&display=swap');

.App{
  display: flex;
  flex-wrap: wrap;
  font-family: 'Exo 2', sans-serif;
  background-repeat: repeat;
}

.aboutContainer{
  margin: 3.61%;
  font-size: 2.2em;
  color: #fff;
  margin-bottom: 0;
}

.bgImage{
  min-width: 100%;
  height: 555px;
  overflow: hidden;
}

.typedText{
  color: #fff;
  position: absolute;
  top: 30%;
  font-size: 40px;
  font-weight: 600;
  left: 10px;
}

.video{
  width: 100%;
  filter: brightness(40%);
}

.kj{
  font-size: 52px;
}

.aboutUsData{
  margin: 3.61%;
  color: white;
  font-size: 20px;
  text-align: center;
  margin-bottom: 20px;
}

.contactContainer{
  margin: 3.61%;
  font-size: 2.2em;
  color: #fff;
  margin-bottom: 0;
  width: 100%
}

.contactIcons{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 3.61%;
}

.social{
  display: flex;
  margin: 10px;
  justify-content: center;
  align-items: center;
  height: 80px;
  width: 100%;
  background-color: rgb(119, 47, 47);
  border-radius: 10px;
}

.copyrights{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  background-color: rgb(119, 47, 47);
}